import { redirect } from "next/navigation";
import { prisma } from "~/server/db";
import { Button } from "~/components/ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "~/components/ui/card";
import { Input } from "~/components/ui/input";

async function loginWithCode(formData: FormData) {
  "use server";

  const code = formData.get("code") as string;
  const team = await prisma.team.findFirst({
    where: {
      id: parseInt(code),
    },
  });

  if (team) {
    redirect(`/play/${team.id}`);
  }

  return { error: "Invalid team code" };
}

export default function Home() {
  return (
    <div className="flex min-h-screen flex-col items-center justify-center p-4 bg-gradient-to-b from-background/50 to-background">
      <Card className="w-full max-w-md">
        <CardHeader className="text-center space-y-2">
          <CardTitle className="text-3xl font-bold tracking-tight">ARG Game</CardTitle>
          <CardDescription>Enter your team code to start playing</CardDescription>
        </CardHeader>
        <CardContent>
          <form action={loginWithCode} className="space-y-4">
            <div className="space-y-2">
              <label htmlFor="code" className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                Team Code
              </label>
              <Input
                id="code"
                name="code"
                type="text"
                placeholder="Enter your team code"
                required
                className="w-full"
              />
            </div>
            <Button type="submit" className="w-full">
              Start Playing
            </Button>
          </form>
        </CardContent>
        <CardFooter className="flex justify-center">
          <Button variant="link" asChild>
            <a href="/admin">Admin Login</a>
          </Button>
        </CardFooter>
      </Card>
    </div>
  );
}
